import Axios from "axios";
import {api} from '../../package.json';
import showNotification from "../messages";


const url = process.env['NODE_ENV']==='development' ? api.dev : api.prod;

export const listaAccesos = () =>{

    return dispatch=>{
        setTimeout(dispatch({type:'ADD_ACCESS',value:accesos}));
    }
}

export const getUsuarios = async () =>{
    try{
        const res = await Axios.get(`${url}/api/sesion/`);
        return res.data.data;
    }catch(err){
        showNotification({
            tag:'Error Accesos control',
            body:'No se pudo obtener la informacion de los usuarios.'
        });
        return [];
    }
}

